<template>
  <div>
    <van-button
      class="top_btn"
      @click="addChild"
    >+ 添加收款账户</van-button>
    <div class="scroll">
      <div
        v-for="(item,i) in payeeList"
        :key="i"
        @click="openMore(item)"
        class='content'
      >
        <div class="payee_left">

          <img
            v-if="item.payment == 2 && item.transfer == 1 "
            class="img_style"
            src="@/assets/two/weixin_round.png"
          >
          <img
            v-else-if="item.payment == 3 && item.transfer == 1"
            class="img_style"
            src="@/assets/two/zhifubao_round.png"
          >
          <img
            v-else-if="item.payment == 12 && item.transfer == 1 "
            class="img_style"
            src="@/assets/two-store.png"
          >
          <img
            v-else
            class="img_style"
            src="@/assets/two/nothing.png"
          >
          <div class="left_name">
            <div class='payee_left'>
              <div style="margin-right:8px">{{ item.transfer == 1 ? '哎哟有房'+item.accountName.split('-')[0]:item.accountName.split('-')[0] }}</div>
              <div
                v-if="item.orderSource == 1 && item.transfer == 1 && (item.payment == 12 || item.payment == 3)"
                class="p8 c_b2"
              >线上</div>
            </div>
            <div :style="{'color':!item.account && item.payment == 2 || !item.account && item.payment == 3 ? '#ff5257':'#b2b2b2'}">{{ !item.account && item.payment == 2 || !item.account && item.payment == 3 ? '未设置' : item.accountName.split('-')[1] }}</div>
          </div>
        </div>
        <div class="payee_left">
          <div>{{ item.accountAmount > 0 ? '¥' + item.accountAmount : '¥' + 0 }}</div>
          <van-icon
            name="arrow"
            color='#b2b2b2'
          />
        </div>
      </div>
    </div>
    <van-popup
      v-model="show_more"
      position="bottom"
      style="width:100%;"
    >
      <div
        v-if="type == 'edit'"
        class="bt"
        @click="goNext('/payeeDetail')"
      >查看</div>
      <div
        v-if="type == 'edit'"
        class="bt"
        @click="editItem"
      >编辑</div>
      <div
        v-if="type == 'edit' && c_item.orderSource == 1 && c_item.transfer != 1 "
        class="bt"
        @click="editHomer"
      >设置为哎哟有房平台收款账户</div>
      <div
        v-if="type == 'edit'"
        class="bt"
        @click="upOrDown('up')"
      >上移</div>
      <div
        v-if="type == 'edit'"
        class="bt"
        @click="upOrDown('down')"
      >下移</div>
      <div
        v-if="type == 'add'"
        class="bt"
        @click="addType = 'aiyo',show_addPerson = true"
      >哎哟有房平台收款账户</div>
      <div
        v-if="type == 'add'"
        class="bt"
        @click="addType = 'outline',show_addPerson = true"
      >线下收款账户</div>
      <div
        v-if="type == 'edit' && c_item.accountAmount == 0 && c_item.origin != 1"
        class="bt"
        @click="removeItem"
      >删除</div>
      <div style="padding:2px;background:#eee;"></div>
      <div
        class="bt"
        style='color:#FF5257;'
        @click='show_more = false'
      >取消</div>
    </van-popup>

    <van-dialog
      v-model="show_addPerson"
      :before-close="confirmAdd"
      show-cancel-button
    >
      <div
        v-if="addType == 'aiyo'"
        class="f16"
      >{{ type == 'edit'?'编辑':'添加' }}账户</div>
      <div>
        <van-cell-group>
          <van-field
            v-if="addType == 'outline'"
            v-model="dataAdd.accountName"
            placeholder="请输入收款账户名称"
          />
          <div
            v-if="addType == 'aiyo'"
            class="aiyo_box"
          >
            <div class="w30">收款方式</div>
            <van-radio-group
              @change='choosePayment'
              style="width:60%;"
              icon-size='18px'
              v-model="payment"
              direction="horizontal"
              checked-color='#ff7d41'
            >
              <van-radio name="2">微信</van-radio>
              <van-radio name="3">支付宝</van-radio>
            </van-radio-group>
          </div>
          <div
            v-if="addType == 'aiyo' && type == 'add'"
            class="aiyo_box"
            @click="editAiyo = !editAiyo"
          >
            <div
              class="w30"
              style="width:100%;"
            >设置成哎哟有房收款账户</div>
            <van-icon
              size='20px'
              :color="editAiyo == true?'#ff7d41':''"
              :name="editAiyo == true?'checked':'circle'"
            />
          </div>
          <div
            v-if="addType == 'aiyo'"
            class="aiyo_box"
          >
            <div class="w30">手机号</div>
            <div style="background:#f2f2f2;width:60%;display:flex;align-items:center;">
              <input
                style="background:#f2f2f2;width:85%padding:10px 0;width:70%;padding:10px;"
                placeholder="请输入手机号"
                type="text"
                v-model="phone"
              />
              <van-icon
                style="width:10%"
                size='20px'
                name="search"
              />
            </div>
          </div>
          <div
            v-if="addType == 'aiyo' && phoneInfo"
            class="aiyo_box"
          >
            <div class="w30">搜索结果</div>
            <div
              style="width:60%;display:flex;align-items:center;"
              @click="choosed = !choosed "
            >
              <van-icon
                size='20px'
                style="padding-right:8px;"
                :color="choosed == true?'#ff7d41':''"
                :name="choosed == true?'checked':'circle'"
              />
              <div>{{ phoneInfo.realName }}&emsp;{{ phoneInfo.phone }}</div>
            </div>
          </div>
        </van-cell-group>
      </div>
    </van-dialog>
  </div>
</template>
<script>
import { Dialog, Toast } from 'vant';
import {
  GetBusinessHotelPayeeAccountByhotel,
  GetBusinessHotelPayeeAccountByphone,
  PostBusinessHotelPayeeAccountAdd,
  PostBusinessHotelPayeeAccountDown,
  PostBusinessHotelPayeeAccountEdit,
  PostBusinessHotelPayeeAccountRemove,
  PostBusinessHotelPayeeAccountTransfer,
  PostBusinessHotelPayeeAccountUp
} from '@/api/api_business.js';

export default {
    data() {
        return {
            dataAdd: {
                hotelId: this.$route.query.hotelId,
                accountName: null,
                userId: null,
                payment: null,
                transfer: null
            },
            c_accountName: null,
            type: null,
            addType: null,
            phone: null,
            c_item: null,
            payment: 2,
            payeeList: [],
            phoneInfo: null,
            editAiyo: false,
            choosed: false,
            show_more: false,
            show_unit: false,
            show_addPerson: false
        };
    },
    watch: {
        phone(neww) {
            if (neww && neww.length == '11') {
                let data = {
                    phone: neww,
                    payment: this.payment
                };
                GetBusinessHotelPayeeAccountByphone(data).done((res) => {
                    this.phoneInfo = res.data;
                    if (this.phoneInfo) {
                        this.dataAdd.userId = this.phoneInfo.id;
                        this.dataAdd.payment = this.payment;
                    } else {
                        Toast({
                            message: '暂无数据',
                            duration: 1000
                        });
                    }
                });
            }
        }
    },
    created() {
        this.$store.commit('setHeader', '收款账户');
        this.getPayeeList();
    },
    methods: {
        getPayeeList() {
            GetBusinessHotelPayeeAccountByhotel({ hotelId: this.$route.query.hotelId }).done((res) => {
            // const tempSorted = res.data ?  res.data.sort((a, b) => {
            //     if (a.tranfer == 1) {
            //       return -1 && a.payment - b.payment;
            //     } else if (b.tranfer == 1) {
            //       return 1 && a.payment - b.payment;
            //     } else {
            //       return a.payment - b.payment;
            //     }
            //   }) : [];
            const ttt1 = res.data ?  res.data.filter(item => item.transfer == 1) : [];
            const ttt2 = res.data ?  res.data.filter(item => item.transfer != 1) : [];
              this.payeeList = ttt1.concat(ttt2);
            });
        },
        addChild() {
            this.type = 'add';
            this.dataAdd.accountName = null;
            this.payment = null;
            this.editAiyo = false;
            this.phone = null;
            this.phoneInfo = null;
            this.show_more = true;
        },
        openMore(item) {
            this.type = 'edit';
            this.dataAdd.accountName = null;
            this.payment = null;
            this.editAiyo = false;
            this.phone = null;
            this.phoneInfo = null;
            this.c_item = item;
            this.show_more = true;
        },
        goNext(url) {
            this.$router.push({
                path: url,
                query: {
                    accountName: this.c_item.accountName,
                    accountAmount: this.c_item.accountAmount,
                    id: this.c_item.id,
                    hotelId: this.$route.query.hotelId
                }
            });
        },
        choosePayment() {
            this.phoneInfo = null;
            this.phone = null;
        },
        editHomer() {
            PostBusinessHotelPayeeAccountTransfer({ hotelPayeeAccountId: this.c_item.id }).done((res) => {
                this.show_more = false;
                Toast({
                    message: res.msg,
                    duration: 1000
                });
                this.getPayeeList();
            });
        },
        editItem() {
            if (this.c_item.orderSource == '1') {
                this.addType = 'aiyo';
                this.payment = this.c_item.payment + '';
                this.editAiyo = this.c_item.transfer == '1' ? true : false;
            } else {
                this.addType = 'outline';
                this.dataAdd.accountName = this.c_item.accountName;
            }
            this.show_addPerson = true;
        },
        upOrDown(title) {
            let _api = title == 'up' ? PostBusinessHotelPayeeAccountUp : PostBusinessHotelPayeeAccountDown;
            _api({ hotelPayeeAccountId: this.c_item.id }).done((res) => {
                this.show_more = false;
                Toast({
                    message: res.msg,
                    duration: 900
                });
                this.getPayeeList();
            });
        },
        removeItem() {
            Dialog.confirm({
                message: `是否删除${this.c_item.accountName}?`
            })
                .then(() => {
                    PostBusinessHotelPayeeAccountRemove(this.c_item.id).done((res) => {
                        Toast(res.msg);
                        this.show_more = false;
                        this.getPayeeList();
                    });
                })
                .catch(() => {});
        },
        confirmAdd(action, done) {
            if (action == 'confirm') {
                done(false);
                if (this.addType == 'outline' && !this.dataAdd.accountName) {
                    Toast({
                        message: '请输入收款账户名称',
                        duration: 900
                    });
                    return;
                }
                if (this.addType == 'aiyo' && !this.phone) {
                    Toast({
                        message: '请输入手机号',
                        duration: 900
                    });
                    return;
                }
                if (this.addType == 'aiyo' && this.phone && !this.phoneInfo) {
                    Toast({
                        message: '请重新输入手机号',
                        duration: 900
                    });
                    return;
                }
                if (this.addType == 'aiyo' && this.phone && this.phoneInfo && this.choosed == false) {
                    Toast({
                        message: '请勾选手机号',
                        duration: 900
                    });
                    return;
                }
                this.dataAdd.transfer = this.editAiyo == true && this.addType == 'aiyo' ? '1' : '0';
                if (this.type == 'add') {
                    if (this.addType == 'outline') {
                        delete this.dataAdd.payment;
                        delete this.dataAdd.userId;
                        delete this.dataAdd.transfer;
                    } else {
                        delete this.dataAdd.accountName;
                    }
                }
                if (this.type == 'edit') {
                    if (this.c_item.orderSource != '1') {
                        delete this.dataAdd.payment;
                        delete this.dataAdd.transfer;
                        this.dataAdd.userId = this.c_item.id;
                    } else {
                        delete this.dataAdd.accountName;
                        delete this.dataAdd.hotelId;
                        this.dataAdd.id = this.c_item.id;
                    }
                }
                let _api = this.type == 'add' ? PostBusinessHotelPayeeAccountAdd : PostBusinessHotelPayeeAccountEdit;
                _api(this.dataAdd).done((res) => {
                    Toast(res.msg);
                    this.dataAdd.accountName = null;
                    this.getPayeeList();
                });
                this.show_more = false;
                this.show_addPerson = false;
            } else if (action == 'cancel') {
                done(false);
                this.show_more = false;
                this.show_addPerson = false;
            }
        }
    }
};
</script>
<style scoped>
.scroll {
    position: fixed;
    overflow: scroll;
    left: 0;
    right: 0;
    height: 78%;
    font-size: 14px;
}
.content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    background: #fff;
    border-bottom: 1px solid #e5e5e5;
}
.payee_left {
    display: flex;
    align-items: center;
}
.left_name {
    text-align: left;
    line-height: 20px;
}
.asada {
    display: flex;
    font-size: 12px;
    color: #ff7d41;
}
.img_style {
    height: 25px;
    width: 25px;
    margin-right: 8px;
}
.p8 {
    border-radius: 2px;
    margin-right: 10px;
    border: 1px solid #ff7d41;
    padding: 0 4px;
    font-size: 12px;
}
.c_b2 {
    color: #b2b2b2;
    border: 1px solid #b2b2b2;
}
/deep/ .van-cell__title {
    text-align: left;
}
/deep/ .van-cell {
    border-bottom: 1px solid #e4e4e4;
    display: flex;
    align-items: center;
}
.bt {
    font-size: 14px;
    padding: 10px 0;
}
.aiyo_box {
    display: flex;
    align-items: center;
    padding: 14px 20px;
    font-size: 14px;
    justify-content: space-between;
}
.w30 {
    width: 30%;
    text-align: left;
}
.w50 {
    display: flex;
    align-items: center;
    width: 60%;
    background: #f2f2f2;
    padding: 10px 0;
}
.f16 {
    font-size: 16px;
    padding: 12px;
    font-weight: bold;
}
.f14 {
    font-size: 14px;
    padding: 14px;
    border-radius: 10px;
    line-height: 30px;
}
.p10 {
    font-size: 14px;
    padding: 10px 0;
    border-bottom: 1px solid #e6e6e6;
}
.normal {
    height: 4px;
    background: #e6e6e6;
}
.top_btn {
    background: #fff;
    border: #fff;
    color: #ff7d41;
    width: 100%;
    margin: 10px 0;
}
</style>



